/* #### Generated By: http://www.cufonfonts.com #### */
@font-face {
    font-family: 'SEGA LOGO FONT';
    font-style: normal;
    font-weight: normal;
    src: local('SEGA LOGO FONT'), url('SEGA.woff') format('woff');
}

.main-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

div h1 {
    font-family: 'SEGA LOGO FONT';
    font-weight: 200;
    font-size: 50px;
    color: rgb(126, 223, 70);
}

.searchBox {
    padding: 40px 0 20px 0;
    height: 10vh;
}

.searchBox input {
    height: 40px;
    background-color: rgb(175, 222, 222);
    border: none;
    border-radius: 4px;
    padding-left: 20px;
}

.searchBox input:hover {
    transform: scale(1.05);
    transition: 0.5s;
}

.con-scroll {
    overflow: scroll;
    border: 5px solid black;
    height: 500px;
}

.con-scroll::-webkit-scrollbar {
    display: none;
}

.cards-container {
    max-width: 1700px;
    margin: 0px 0 0px 10px;
    display: grid;
}

.card {
    margin: 20px;
    border-radius: 8px;
    max-width: 20rem;
    height: 22rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: lightblue;
}

.card:hover {
    transform: scale(1.1);
    transition: 0.5s;
}

@media (min-width: 300px) {
    .cards-container {
        grid-template-columns: repeat(1, 1fr);
    }

    div h1 {
        font-size: 25px;
    }

    .searchBox {
        padding: 10px 0 20px 0;
    }

}

@media (min-width: 600px) {
    .cards-container {
        grid-template-columns: repeat(2, 1fr);
    }

    div h1 {
        font-size: 35px;
    }

    .searchBox {
        padding: 20px 0 20px 0;
    }
}

@media (min-width: 900px) {
    .cards-container {
        grid-template-columns: repeat(3, 1fr);
    }

    div h1 {
        font-size: 35px;
    }

    .searchBox {
        padding: 30px 0 20px 0;
    }
}

@media (min-width: 1200px) {
    .cards-container {
        grid-template-columns: repeat(4, 1fr);
        width: 95vw;
    }

    div h1 {
        font-size: 50px;
    }

    .searchBox {
        padding: 30px 0 20px 0;
    }
}